import { Action } from "sprotty/lib";

/**
 * Action used to toggle a filter.
 *
 * @author hmarchadour
 */
export class ToggleFilterAction implements Action {
  /**
   * The kind of the action.
   */
  public static readonly KIND: string = "toggleFilter";

  /**
   * The kind of the action.
   */
  public readonly kind: string = ToggleFilterAction.KIND;

  /**
   * The name of the filter.
   */
  public readonly filterName: string;

  /**
   * The new state of the filter.
   */
  public readonly newState: boolean;

  /**
   * The constructor.
   * @param filterName The name of the filter
   * @param newState The new state of the filter
   */
  constructor(filterName: string, newState: boolean) {
    this.filterName = filterName;
    this.newState = newState;
  }
}
